@import url(https://fonts.googleapis.com/css2?family=Josefin+Slab:ital,wght@1,300&family=Libre+Bodoni:ital@1&family=Padauk&family=Poppins:wght@300;400;500;600&family=Square+Peg&family=Ubuntu:wght@300&display=swap);
header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header_container{
text-align: center;
height: 100%;
position: relative;
}

/********************  CTA ******************/

.cta{
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
    display: flex;
    grid-gap: 1.4rem;
    gap: 1.4rem;
    justify-content: center;
}

/********************  Header Socials ******************/
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 5rem;
}

.myself{
    background: content-box linear-gradient(#4db5ff,transparent);
    border-radius: 12rem 12rem 0 0;
    width: 15rem;
    height: 15rem;
    display: flex;
    text-align: center;
    justify-content: center;
    margin: auto;
    overflow: hidden;
}

.scrolll_down{
    position: absolute;
    right:-2.5rem;
    bottom: 7rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-size: 0.9rem;
}


/******************Media Queries (Media Devices) ******************************/

@media screen and (max-width:1024px) {
    header{
        height: 140vh;
    }
}

/******************Media Queries (Small Devices) ******************************/
@media screen and (max-width:600px) {
    header{
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        width: var(--container-width-outer);
    }

     .header_socials,.scrolll_down{
        display: none;
    }

    
}


nav{
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    height: 7vh;
    display: block;
    padding: 0.3rem 0.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    bottom: 1rem;
    display: flex;
    border-radius: 1.5rem;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.5rem;
    display: flex;
    /* color: var(--color-light); */
    font-size: 1.3rem;
    border-radius: 50%;
}

nav a:hover{
    background:rgba(0,0,0,0.3);
}

nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}
.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about_me{
    height: 21rem;
    width: 16rem;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: content-box linear-gradient(45deg ,transparent,#4db5ff);
    display: grid;
    place-items: center;
}

.about_me_img{
    background: #4db5ff;
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    height: 20rem;
    width: 15rem;
    box-shadow: 5px 5px 5px 5px #01192b;
    transition: var(--transition);
}

.about_me_img:hover{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
#aboutImg{
    height: 20rem;
    width: 15rem;
    border-radius: 2rem;
    overflow: hidden;
}

.about_cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about_card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    justify-content: center;
    transition: var(--transition);
}

.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-varient);
    cursor: pointer;
}

.about_icon{
    color: var(--color-primary);
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.about_card h5{
    font-size: 0.95rem;
}

.about_card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about_content p{
    margin: 2rem 0 2.6rem;
    text-align: center;
    height: 3rem;
    width: 35rem;
    font-size: 1rem;
}

/******************Media Queries (Media Devices) ******************************/

@media screen and (max-width:1024px) {
    .about_container{
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    
    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about_content p{
        margin: 1rem 0 1.5rem;
    }
}

/******************Media Queries (Small Devices) ******************************/
@media screen and (max-width:600px) {
    #about{
        width: var(--container-width-outer);
    }
    .about_me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about_cards{
        grid-template-columns: 1fr ;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about_content{
        text-align: center;
    }

    .about_content p{
        /* margin: 1.5rem 0;  ----change  */
        margin: 1.5rem auto; 
    }
}

@media screen and (max-width:375px) {
    .about_cards {
        grid-template-columns: 0.8fr ;
        justify-content: space-around;
    }
}
.experience_container{
   display: grid;
   grid-template-columns: 1fr 1fr;
   grid-gap: 2rem;
   gap: 2rem;
}

.experience_container > div{
    background: var(--color-bg-variant);
    padding: 2.5rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
    margin: auto;
}

.experience_container :hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience_container >div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience_content{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    grid-column-gap: 4rem;
    -webkit-column-gap: 4rem;
            column-gap: 4rem;
    justify-content: center;
}

.experience_detail{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experienced_details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}


/******************Media Queries (Media Devices) ******************************/

@media screen and (max-width:1024px) {
    .experience_container{
        grid-template-columns: 1fr;
    }

    .experience_container > div{
        /* width: 80%; */
        padding: 2rem;
        margin-top: 0 auto;
    }

    .experience_content{
        padding: 1rem;

    }
}

/******************Media Queries (Small Devices) ******************************/
@media screen and (max-width:600px) {
        #experience{
            width: var(--container-width-outer);
        }
        .experience_container{
            grid-gap: 4rem;
            gap: 4rem;
        }

        .experience_container > div{
            width: 100%;
            padding: 2rem 6rem;
        }


        .experience_content{
            grid-column-gap: 1rem;
            -webkit-column-gap: 1rem;
                    column-gap: 1rem;
        }
}

.services_container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 3rem;
    gap: 3rem;
}

.services{
    background: var(--color-bg-variant) ;
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    transition: var(--transition);
}

.services:hover{
    background: transparent;
    border-bottom: var(--color-primary-variant);
    cursor: default;
}

.services_head{
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0 0 0 0.1);
}

.services_head h3{
    color: var(--color-bg);
    text-align: center;
    font-size: 1rem;
}

.service_list{
    padding: 2rem;
}

.service_list li{
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    margin-bottom: 0.8rem;
}
.services_list-icon{
    color: var(--color-primary);
    margin-top: 2px;
    font-size: 0.9rem;
}

.service_list p{
    font-size: 0.9rem;
}



/******************Media Queries (Media Devices) ******************************/

@media screen and (max-width:1024px) {
    .services_container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }

    .services{
        height: auto;
    }
}
/******************Media Queries (Small Devices) ******************************/
@media screen and (max-width:600px) {
    #services{
        width: var(--container-width-outer);
    }
    .services_container{
        grid-template-columns: 1fr;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
}
.porject_container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
    justify-content: space-around;
}

.project_item{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.project_item:hover{
    background-color: var(--color-primary-variant);
    background: transparent;
}

.project_item-img{
    border-radius: 1.5rem;
    overflow: hidden;
}

.project_item h3{
    margin: 1.2rem 0 2rem;
}

.project_item-cta{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

/******************Media Queries (Media Devices) ******************************/

@media screen and (max-width:1024px) {
    .porject_container{
        grid-template-columns: 0.8fr 0.8fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}
/******************Media Queries (Small Devices) ******************************/
@media screen and (max-width:600px) {
    #project{
        width: var(--container-width-outer);
    }
    .porject_container{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.container.acheivements_container{
    width: 55%;  
    
}


/******************Media Queries (Small Devices) ******************************/
@media screen and (max-width:600px) {
    #acheivements{
        width: var(--container-width-outer);
    }
    .container.acheivements_container{
        width: 80%;
    }
}

.container.contect_container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}

.cotect_options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contect_option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contect_option:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contect_option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contect_option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent ;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--clolor-white);
    font-size: 1rem;
}


/******************Media Queries (Media Devices) ******************************/

@media screen and (max-width:1024px) {
    .container.contect_container{
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;

    }
}

/******************Media Queries (Small Devices) ******************************/
@media screen and (max-width:600px) {
    #contect{
        width: var(--container-width-outer);
    }

}
