*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
:root{
    --color-bg:#1f1f38;
    --color-bg-variant:#2c2c6c;
    --color-primary:#4db5ff;
    --color-primary-variant:rgba(77,181,255,0.4);
    --color-white:#fff;
    --color-light:rgba(255,255,255,0.6);

    --transition:all 400ms ease;

    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-sm:90%;  
    --container-width-outer:110vh;
}
html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    background: #1f1f38;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    /* background-image: url(../src/assets/projectImg-removebg-preview.png); */
}

.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section{
    margin-top: 2rem;
    /* height: 75vh; */
}

section > h2, section > h5{
    text-align: center;
    -webkit-columns: rgba(255,255,255,0.6);
            columns: rgba(255,255,255,0.6);
    -webkit-columns: var(--color-light);
            columns: var(--color-light);
}
section > h2{
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: rgba(255,255,255,0.6);
    color: var(--color-light);
}

a{
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}
a:hover{
    color: #fff;
    color: var(--color-white);
}

.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.7rem 1.2rem;
    margin: 1rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}
.btn:hover{
    background:#fff;
    background:var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: #4db5ff;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg);
}
img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/******************Media Queries (Media Devices) ******************************/

@media screen and (max-width:1024px) {
    .containar{
        width: 86%;
        width: var(--container-width-md);
    }

    section{
        margin-top: 6rem;
    }
}

/******************Media Queries (Small Devices) ******************************/
@media screen and (max-width:600px) {
    .containar{
        width: 90%;
        width: var(--container-width-sm);
    }

    section >h2{
        margin-top: rem;
    }
}


